* {
    box-sizing: border-box;
}
html,body {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0;
}
body{
    overflow: hidden;
    display: flex;
    flex-direction: column;
    background-color: lightblue;
}

#maincontainer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    flex-shrink: 1;
    column-gap: 50px;
    width: 100vw;
    height: 100vh;
    
    &.mini {
        height: 60px;
        width: fit-content;
        margin: 0 auto;
    }
    .main_icon {
        box-sizing: border-box;
        width: 80px;
        height: 50px;
        background-repeat: no-repeat;
        background-position: center top;
        background-size: contain;
        cursor: pointer;
        color: #666;

        &:hover{
            outline: 1px dashed #999;
            box-shadow: 0px -10px 50px #ffee00;
            background-color: #fff;
        }
        &.selected {
            outline: 2px dashed rgb(0, 84, 112);
            box-shadow: 0px -10px 50px rgb(0, 84, 112);
            background-color: #fff;
        }
        &::after {
            margin-top: 80%;
            display: block;
            text-align: center;
            font-size: 12px;
        }

        &#computer-btn {
            background-image: url('./icons/computer.png');
            &::after {
                content: 'View files';
            }
        }
        &#runbatch-btn {
            background-image: url('./icons/batch_samples.png');
            &::after {
                content: 'Run Batch Samples';
            }
        }
        &#runmanual-btn {
            background-image: url('./icons/sm_manual_sample.png');
            &::after {
                content: 'Run Manual Sample';
            }
        }
        &#runqc-btn {
            background-image: url('./icons/qc_samples.png');
            &::after {
                content: 'Run QC Samples';
            }
        }

    }
}

#monitor {
    display: none;
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 100vh;
    max-height: calc(100vh - 60px);
    overflow: hidden;
    border: 5px solid #333;
    border-radius: 5px;
    padding: 5px;
    background-color: #000;

    #top-menu, #bot-menu{
        width: 100%;
        height: 60px;
        background-color: #eee;

        button {
            padding: 0.6em;
            cursor: pointer;
        }
        button.selected{
            background-color: rgb(112, 198, 255);
            font-weight: 600;
        }
        #back-btn {
            display: none;
            float: right;
        }
    }
    #content {
        width: 100%;
        height: calc(100% - 100px);
        overflow-y: auto;
        overflow-x: hidden;
        background-color: #eee;
        outline: 1px solid #555;
        box-sizing: border-box;
        #graphscontainer {
            display: flex;
            flex-direction: column;
            background-color: #fff;

            .graphrowheader {
                width: calc(100% -10px);
                height: 1em;
                display: table;
                background-color: #fff;
                position: absolute;
                top: 45px;
                left: 80px;
                z-index: 999;

                .graphrowheaderitem {
                    display: table-cell;
                    text-align: center;
                    font-size: 12px;
                    font-weight: bold;
                    border: 1px solid #555;
                    padding: 5px;   
                }
            }
            .graphrow {
                font-size:  12px;
                position: relative;
                height: 100px;
                width: 100%;
                display: flex;
                flex-direction: row;
                border: 1px solid #555;
            
                .titleCol{
                    width: 20px;
                    .ytitle{
                        font-size: 10px;
                        writing-mode: vertical-rl;
                        text-orientation: mixed;
                        text-align: center;
                        height: 100%;
                    }
                }
                

                .graph{
                    width: calc(100% - 150px)
                }
                .dataSummary {
                    height: 100%;
                    flex: 1;
                    
                    table {
                        width: 100%;
                        height: 100%;
                        text-align: center;
                        td:nth-child(1) {
                            width: 50%;
                        }
                    }
                }
            }
        }
        .tableFixHead {
            overflow-y: auto;
            height: 100%;
        
            >table {
                display: none;
                width:100%;
                text-align: center;
                border-collapse: collapse;
                th {
                    position: sticky;
                    top: 0px;
                    background: #eee;                
                }
                th:first-child, td:first-child{
                    border-right: 1px solid #555;
                    font-size: 18px;
                }
                tr {
                    height: 26px;
                }
                /* striped rows */
                tbody tr:nth-of-type(odd) { 
                    background-color:#ccc;
                }
                /* hover row */
                tbody tr:hover {
                    background-color: #fffb00;
                    cursor: pointer;
                }

            }
        }
    }

}

#samplesContainerMenu{
    box-sizing: border-box;
    flex: 1;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #eee;
}


